<template>
  <div class="Main">
    <!--<starFlow></starFlow>-->
    <ul>
        <li><router-link to="/first">一个</router-link></li>
        <li><router-link to="/second">两个</router-link></li>
        <li><router-link to="/Hello">三个</router-link></li>
        <li><router-link to="/starFlow">调到满天星</router-link></li>
      </ul>
    <router-view></router-view>
  </div>
</template>

<script>
  import starFlow from '../Main/starFlow'
  import first from '../child/first'
  import second from '../child/second'
  import Hello from '../child/Hello'

  export default {
    name: 'Main',
    components:{
      starFlow
    },
    data () {
      return {
        msg: 'Welcome to Your Vue.js App'
      }
    }
  }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less">
  @color {
     background: #000000;
     color: #ffffff;
  }
  #Main{
    color: @color;
  }
  h1, h2 {
    font-weight: normal;
  }

  ul {
    list-style-type: none;
    padding: 0;
  }

  li {
    display: inline-block;
    margin: 0 10px;
  }

  a {
    color: #42b983;
  }
</style>



